import React from "react";
import { PageProps } from "gatsby";
import Header from "../componets/header/Header";
import Footer from "../componets/footer/Footer";
import EventHero from "../componets/EventHero/EventHero";
import EventSection from "../componets/EventAbout/EventAbout";
import SponsorsSection from "../componets/SponsorsSection/SponsorsSection";
import TestimonialsSection from "../componets/TestimonialsSection";
import EventSpeakers from "../componets/EventSpeakers/EventSpeakers";
import PriceList from "../componets/PriceList/PriceList";
import EventAttendees from "../componets/EventAttendees/EventAttendees";
import { useLanguage } from "../context/LanguageContext";
import { getContent } from "../content/content";

const EventPage: React.FC<PageProps> = () => {
  const { lang } = useLanguage();
  const globalContent = getContent(lang);
  const content = globalContent.pages.landing.section.eventContent;

  const info = content?.info || {};
  const eventData = {
    eventName: info.eventName || "PHP CONFERENCE IN ISTANBUL",
    eventDate: info.eventDate || "2025-12-25T09:00:00",
    location: info.location || "3200 Barbaros Bulvan Besiktas/Istanbul, TR",
    remainingTickets: info.remainingTickets ?? 245,
    speakersCount: info.speakersCount ?? 24,
  };

  const handleScrollToPricing = () => {
    const el = document.getElementById("pricing");
    if (el) el.scrollIntoView({ behavior: "smooth" });
  };

  return (
    <main style={{ background: '#1A202C', minHeight: '100vh' }}>
      <Header content={globalContent?.pages?.globals?.section?.headerContent} />
      <EventHero
        eventName={eventData.eventName}
        eventDate={eventData.eventDate}
        location={eventData.location}
        remainingTickets={eventData.remainingTickets}
        speakersCount={eventData.speakersCount}
        bgImage={content?.hero?.bgImage}
        onRegisterClick={handleScrollToPricing}
      />
      <EventSection />
      <EventSpeakers />
      <SponsorsSection content={{ sponsors: [] }} />
      <TestimonialsSection />
      <PriceList />
      <EventAttendees />



      <Footer content={globalContent?.pages?.globals?.section?.footerContent} />
    </main>
  );
};

export default EventPage;

export const Head = () => (
  <>
    <title>PHP Conference Istanbul 2024 - Event Registration</title>
    <meta name="description" content="Join PHP Conference Istanbul 2024 featuring industry leaders and professional speakers." />
  </>
);